import axios from "axios";
import create from "zustand";

interface IUseAuth {
  userData: any;
  isAuthorized: boolean;
  role: "Facility" | "Employee" | string;
  setUserData: (data: any, role: string) => void;
  logoutUser: () => void;
}

export const useAuth = create<IUseAuth>((set) => ({
  userData: {},
  isAuthorized: false,
  role: "",
  setUserData: (data: any, role) =>
    set((state) => ({
      ...state,
      isAuthorized: true,
      userData: data,
      role,
    })),
  logoutUser: () => {
    localStorage.removeItem("@auth_token");
    delete axios.defaults.headers.common.Authorization;
    set((state) => ({
      ...state,
      isAuthorized: false,
      userData: {},
      role: "",
    }));
  },
}));
