import { isEmpty, pickBy } from "lodash";
import { FC, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import { LeftArrowIcon, RightArrowIcon } from "../../../icons";
import useLanguage from "../../../shared/hooks/useLanguage";
import { StyledButton, StyledText } from "../../../shared/StyledComponents";
import { parse, stringify } from "querystring";
import SingleEmployee from "./SingleEmployee";
import { useQuery } from "react-query";
import axios from "axios";
import { websiteId } from "../../../baseURL";
import { useAuth } from "../../../auth/auth.state";

export interface EmployeeType {
  id: number;
  main_image: string;
  address: string;
  first_name: string;
  last_name: string;
  email: string;
  phoneNumber: string;
  is_saved: boolean;
  notes: string;
  city: string;
  categories: { id: number; name: string }[];
}

const ListContent: FC = () => {
  const { search } = useLocation();
  const { language } = useLanguage();
  const { role } = useAuth();
  const [searchObj, setSearchObj] = useState({});
  const [totalPages, setTotalPages] = useState(1);
  const [isSaved, setIsSaved] = useState(false);
  const [page, setPage] = useState(0);

  useEffect(() => {
    if (!isEmpty(search)) {
      let searchValue = parse(search);
      setPage(0);
      searchValue.zip = searchValue["?zip"];
      delete searchValue["?zip"];
      searchValue.categories = searchValue.staff_type;
      delete searchValue.staff_type;
      setSearchObj(pickBy(searchValue, (val) => !!val));
    }
  }, [search]);

  const { data: employeesData = { employees: [], totalPages: 0 } } = useQuery(
    ["all_employees", language, page, searchObj, isSaved],
    async () =>
      await axios.get(
        `/listing/employees/${language}/${websiteId}?${stringify({
          ...searchObj,
          pageIndex: page,
          pageSize: 10,
          saved_only: isSaved,
        })}`
      ),
    {
      onSuccess: (data) => {
        setTotalPages(data.totalPages || 1);
      },
      select: (res: any) => {
        return {
          employees: res.data.data,
          totalPages: res.data.total_pages,
        };
      },
    }
  );

  return (
    <div className=" w-full lg:w-8/12 lg:pl-4">
      <div className="w-full flex items-center mt-12 lg:mt-0 justify-between mb-4">
        <StyledText fs={20} lh={30} ls={0.6} fw={700} color="#DA3B19">
          Employee
        </StyledText>
        <div className={role !== "Facility" ? "hidden" : "flex"}>
          <StyledButton
            onClick={() => {
              setIsSaved(false);
              setPage(0);
            }}
            disabled={!isSaved}
            className="mr-2 py-2 px-5"
            bg={!isSaved ? "#ea7112" : "#F2F2F2"}
          >
            <StyledText fw={400} color={!isSaved ? "white" : "black"}>
              All
            </StyledText>
          </StyledButton>
          <StyledButton
            onClick={() => {
              setIsSaved(true);
              setPage(0);
            }}
            className="py-2 px-5"
            disabled={isSaved}
            bg={isSaved ? "#ea7112" : "#F2F2F2"}
          >
            <StyledText fw={400} color={isSaved ? "white" : "black"}>
              Saved List
            </StyledText>
          </StyledButton>
        </div>
      </div>
      <div className="w-full flex flex-1 flex-col mb-8">
        {employeesData?.employees?.map((item: EmployeeType, idx: number) => (
          <SingleEmployee {...item} key={idx} />
        ))}
      </div>
      <div className="flex flex-1 items-center justify-center lg:justify-start">
        <StyledPageBtn onClick={() => setPage(page - 1)} disabled={page === 0}>
          <LeftArrowIcon />
        </StyledPageBtn>
        <PageContainer className="mx-4">
          <StyledText fw={400} color="#000">
            {`${page + 1} of ${totalPages}`}
          </StyledText>
        </PageContainer>
        <StyledPageBtn
          onClick={() => setPage(page + 1)}
          disabled={page + 1 === totalPages}
        >
          <RightArrowIcon />
        </StyledPageBtn>
      </div>
    </div>
  );
};

export default ListContent;

const StyledPageBtn = styled.button`
  width: 60px;
  height: 60px;
  background-color: #ea7112;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const PageContainer = styled.div`
  height: 60px;
  padding: 18px 49px;
  border: 1px solid #ea7112;
  display: flex;
  align-items: center;
  justify-content: center;
`;
