import { isEmpty, pickBy } from "lodash";
import { FC, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import { LeftArrowIcon, RightArrowIcon } from "../../../icons";
import useLanguage from "../../../shared/hooks/useLanguage";
import { StyledText } from "../../../shared/StyledComponents";
import { parse, stringify } from "querystring";
import { useQuery } from "react-query";
import axios from "axios";
import { websiteId } from "../../../baseURL";
import SingleJob from "./SingleJob";

export interface FacilityType {
  id: number;
  main_image: string;
  address: string;
  first_name: string;
  last_name: string;
  email: string;
  phoneNumber: string;
  is_saved: boolean;
  notes: string;
  city: string;
  categories: { id: number; name: string }[];
}

const SavedList: FC = () => {
  const { search } = useLocation();
  const { language } = useLanguage();

  const [searchObj, setSearchObj] = useState({});
  const [totalPages, setTotalPages] = useState(1);
  const [page, setPage] = useState(0);

  useEffect(() => {
    if (!isEmpty(search)) {
      let searchValue = parse(search.replace("?", ""));
      setPage(0);
      setSearchObj(pickBy(searchValue, (val) => !!val));
    }
  }, [search]);

  const { data: facilityData = { facilities: [], totalPages: 0 } } = useQuery(
    ["listing/saved_job", language, page, searchObj],
    async () =>
      await axios.get(
        `/listing/saved_job/${language}/${websiteId}?${stringify({
          ...searchObj,
          pageIndex: page,
          pageSize: 10,
        })}`
      ),
    {
      onSuccess: (data) => {
        setTotalPages(data.totalPages || 1);
      },
      select: (res: any) => {
        return {
          facilities: res.data.data,
          totalPages: res.data.total_pages,
        };
      },
    }
  );

  return (
    <>
      <div className="w-full flex flex-1 flex-col mb-8">
        {facilityData?.facilities?.map((item: FacilityType, idx: number) => (
          <SingleJob {...item} key={idx} />
        ))}
      </div>
      <div className="flex flex-1 items-center justify-center lg:justify-start">
        <StyledPageBtn onClick={() => setPage(page - 1)} disabled={page === 0}>
          <LeftArrowIcon />
        </StyledPageBtn>
        <PageContainer className="mx-4">
          <StyledText fw={400} color="#000">
            {`${page + 1} of ${totalPages}`}
          </StyledText>
        </PageContainer>
        <StyledPageBtn
          onClick={() => setPage(page + 1)}
          disabled={page + 1 === totalPages}
        >
          <RightArrowIcon />
        </StyledPageBtn>
      </div>
    </>
  );
};

export default SavedList;

const StyledPageBtn = styled.button`
  width: 60px;
  height: 60px;
  background-color: #ea7112;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const PageContainer = styled.div`
  height: 60px;
  padding: 18px 49px;
  border: 1px solid #ea7112;
  display: flex;
  align-items: center;
  justify-content: center;
`;
