import { FC, useEffect } from "react";
import styled from "styled-components";
import { StyledButton, StyledText } from "../../../shared/StyledComponents";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import HookTextGlobalInput from "../../../shared/hookForm/HookTextGlobalInput";
import HookCheckbox from "../../../shared/hookForm/HookCheckbox";
import { Link } from "react-router-dom";
import { useAuth } from "../../../auth/auth.state";
import { useMutation } from "react-query";
import axios from "axios";
import { useNavigate } from "react-router-dom";

interface FormTypes {
  username: string;
  password: string;
  keep_me_in: boolean;
}

const schema = yup.object().shape({
  username: yup.string().required("Username is required!"),
  password: yup.string().required("Password is required!"),
  keep_me_in: yup.boolean(),
});

const defaultValues = {
  username: "",
  password: "",
  keep_me_in: false,
};

const LoginForm: FC = () => {
  const { setUserData, isAuthorized } = useAuth();
  const navigate = useNavigate();

  const {
    handleSubmit,
    control,
    formState: { errors },
    reset,
    setError,
  } = useForm<FormTypes>({
    resolver: yupResolver(schema),
    mode: "onChange",
    defaultValues,
  });

  useEffect(() => {
    if (isAuthorized) {
      navigate("/");
    }
  }, [isAuthorized, navigate]);

  const { mutate, isLoading } = useMutation(
    (body: any) => axios.post("/auth/web/login", body),
    {
      onSuccess: (res) => {
        reset(defaultValues);
        const { role, user, token } = res.data?.data;
        setUserData(user, role);
        localStorage.setItem("@auth_token", token);
        axios.defaults.headers.common.Authorization = `bearer ${token}`;
      },
      onError: (err: any) => {
        setError("username", {
          type: "manual",
          message: err?.response?.data?.message,
        });
        setError("password", {
          type: "manual",
          message: err?.response?.data?.message,
        });
      },
    }
  );

  const submitForm = (formVales: FormTypes) => {
    mutate(formVales);
  };

  return (
    <form onSubmit={handleSubmit(submitForm)}>
      <div className="container mx-auto flex flex-col items-center justify-center mt-48 sm:mt-32 lg:mt-6 py-28">
        <Container className="w-8/12 lg:w-5/12 mb-8">
          <div className="w-full mb-9">
            <StyledText fs={20} lh={39} color="#DA3B19">
              Login
            </StyledText>
          </div>
          <div className="w-full flex mb-5">
            <HookTextGlobalInput
              name="username"
              control={control}
              errors={errors}
              label="Username"
            />
          </div>
          <div className="w-full flex mb-5">
            <HookTextGlobalInput
              name="password"
              control={control}
              errors={errors}
              label="Password"
            />
          </div>
          <div className="flex w-full mb-5 items-center">
            <HookCheckbox name="affiliate" control={control} />
            <StyledText fs={15} lh={20} ls={0} color="#000" fw={400}>
              Keep me Logged in.
            </StyledText>
          </div>
          <div className="flex w-full items-center justify-center mb-5">
            <StyledButton
              disabled={isLoading}
              type="submit"
              className="px-20 py-4"
            >
              <StyledText fs={17} fw={700} ls={0.3} color="white">
                Login
              </StyledText>
            </StyledButton>
          </div>
          <Link to="/forgetpassword">
            <div className="flex w-full items-center justify-center">
              <StyledText fw={400} color="#EA7112">
                Forgot Password
              </StyledText>
            </div>
          </Link>
        </Container>
      </div>
    </form>
  );
};

export default LoginForm;

const Container = styled.div`
  padding: 32px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #00000012;
`;

const AvatarContainer = styled.div`
  width: 90px;
  height: 90px;
  border: 1px solid #bababa;
  border-radius: 100%;
  margin-right: 20px;
`;

