import { FC } from "react";
import styled from "styled-components";
import { StyledButton, StyledText } from "./StyledComponents";
import { Link } from "react-router-dom";
import { useAuth } from "../auth/auth.state";

const Header: FC = () => {
  const { isAuthorized, userData, logoutUser } = useAuth();
  return (
    <MainContainer>
      <div className="container py-4 w-full h-full lg:flex md:items-center md:justify-between container mx-auto">
        <Link to="/">
          <ImageContainer className="flex">
            <img src="images/logos/mainLogo.png" alt="logo" />
          </ImageContainer>
        </Link>
        <div className="flex flex-col mt-2 justify-center sm:flex-row sm:mt-0 items-center lg:mt-0 ">
          {isAuthorized ? (
            <>
              <div className="mr-2 self-stretch flex flex-col justify-center items-center">
                <StyledImage
                  src={userData?.main_image}
                  alt={userData?.first_name}
                />
                <div className="ml-2">{`${userData?.first_name} ${userData?.last_name}`}</div>
              </div>
              <StyledButton
                onClick={() => logoutUser()}
                className="px-8 py-5 border-2 border-yellow-600 mt-3 sm:mt-0"
                bg="transparent"
              >
                <StyledText fs={15} fw={700} ls={0.3} color="#EA7112">
                  Logout
                </StyledText>
              </StyledButton>
            </>
          ) : (
            <>
              <Link to="/employeeregister">
                <StyledButton
                  className="px-8 py-5 border-2 border-yellow-600 mt-3 sm:mt-0"
                  bg="transparent"
                >
                  <StyledText fs={15} fw={700} ls={0.3} color="#EA7112">
                    Sign Up Now
                  </StyledText>
                </StyledButton>
              </Link>
              <Link to="/login">
                <StyledButton
                  className="px-8 py-5 mx-6 mt-3 sm:mt-0"
                  bg="#EA7112"
                >
                  <StyledText fs={15} fw={700} ls={0.3} color="white">
                    Employer Login
                  </StyledText>
                </StyledButton>
              </Link>
              <Link to="/login">
                <StyledButton className="px-8 py-5 mt-3 sm:mt-0">
                  <StyledText fs={15} fw={700} ls={0.3} color="white">
                    Employee Login
                  </StyledText>
                </StyledButton>
              </Link>
            </>
          )}
        </div>
      </div>
    </MainContainer>
  );
};

export default Header;

const MainContainer = styled.div`
  // height: 141px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 0px 35px #00000014;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
`;

const ImageContainer = styled.div`
  max-height: 71px;
  max-width: 583px;
`;

const StyledImage = styled.img`
  width: 40px;
  height: 40px;
  border-radius: 100%;
  object-fit: cover;
`;
