import { FC, useEffect } from 'react';
import styled from 'styled-components';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import Checkbox from '../../../shared/form/Checkbox';
import { includes, isEmpty, keys, map, reject } from 'lodash';
import RadiusSelect from './RadiusSelect';
import { useLocation, useNavigate } from 'react-router-dom';
import useLanguage from '../../../shared/hooks/useLanguage';
import { useQuery } from 'react-query';
import qs from 'querystring';
import { websiteId } from '../../../baseURL';
import axios from 'axios';

interface FormTypes {
  radius: string | number;
  categories: number[];
}

interface OptionType {
  value: number;
  label: string;
}

const schema = yup.object().shape({
  radius: yup.string(),
  categories: yup.array().of(yup.number()),
});

const defaultValues = {
  radius: '',
  categories: [],
};

const SearchContent: FC = () => {
  const navigate = useNavigate();
  const { search } = useLocation();
  const { language } = useLanguage();
  const { handleSubmit, setValue, watch, reset } = useForm<FormTypes>({
    resolver: yupResolver(schema),
    mode: 'onChange',
    defaultValues,
  });

  const categories = watch('categories');
  const radius = watch('radius');

  useEffect(() => {
    if (!isEmpty(search)) {
      let searchValues = qs.parse(search.replace('?', ''));
      keys(defaultValues).forEach((val: any) => {
        if (Array.isArray(searchValues[val]) && !isEmpty(searchValues[val])) {
          return setValue(
            val,
            map(searchValues[val], (o) => +o),
            { shouldDirty: true, shouldValidate: true }
          );
        }
        if (!isEmpty(searchValues[val])) {
          setValue(val, searchValues[val], {
            shouldDirty: true,
            shouldValidate: true,
          });
        }
      });
    }
  }, [search, setValue]);

  const { data: categoriesList = [] } = useQuery(
    ['type_of_staff', language, websiteId],
    () =>
      axios.get(
        `/category/sub_categories_published/${language}/${websiteId}?categoryType=CaringDay.Model.Employee`
      ),
    {
      select: (res) => {
        return res.data.data.map((val: any) => {
          return {
            value: val.id,
            label: val.name,
          };
        });
      },
      enabled: !!language,
    }
  );

  const submitSearch = (formData: any) => {
    navigate({
      pathname: '/joblisting',
      search: qs.stringify(formData),
    });
    reset(defaultValues);
  };

  return (
    <form
      onSubmit={handleSubmit(submitSearch)}
      className="w-full xl:w-4/12 mt-40 sm:mt-20 xl:mt-0 xl:pr-4"
    >
      <Container>
        <div className="w-full mb-2">
          <StyledText fs={20} lh={25} ls={0.6} fw={700} color="#DA3B19">
            Filters
          </StyledText>
        </div>
        <div className="w-full mb-5">
          <StyledText className="mb-3" color="#000" fw={500}>
            Search Radius
          </StyledText>
          <div className="w-full flex flex-wrap">
            {radiusList?.map((val) => (
              <RadiusSelect
                key={val.value}
                label={val.label}
                isActive={val.value === radius}
                onChange={() =>
                  setValue('radius', val.value, {
                    shouldDirty: true,
                    shouldValidate: true,
                  })
                }
              />
            ))}
          </div>
        </div>
        <div className="w-full mb-9">
          <StyledText className="mb-5" color="#000" fw={500}>
            Type Of Job
          </StyledText>
          {categoriesList?.map((val: OptionType) => (
            <div className="w-full flex items-center mb-4" key={val.value}>
              <Checkbox
                checked={includes(categories, val.value)}
                onChange={() => {
                  if (includes(categories, val.value)) {
                    setValue(
                      'categories',
                      reject(categories, (o) => o === val.value),
                      { shouldDirty: true, shouldValidate: true }
                    );
                  } else {
                    setValue('categories', [...categories, val.value], {
                      shouldDirty: true,
                      shouldValidate: true,
                    });
                  }
                }}
              />
              <StyledText color="#000" fs={15} lh={20} fw={400} ls={0}>
                {val.label}
              </StyledText>
            </div>
          ))}
        </div>
        <div className="w-full flex items-center justify-center">
          <StyledButton type="submit" bg="#EA7112" className="py-4 px-24">
            <StyledText ls={0.3} color="#fff">
              Search
            </StyledText>
          </StyledButton>
        </div>
      </Container>
    </form>
  );
};

export default SearchContent;

const Container = styled.div`
  flex: 1px;
  padding: 23px 24px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 20px #00000010;
`;

const radiusList = [
  {
    value: '10',
    label: '10 Miles',
  },
  {
    value: '25',
    label: '25 Miles',
  },
  {
    value: '50',
    label: '50 Miles',
  },
];

